.container {
    display: grid;
    grid-template-columns: 1fr; /* 1 column by default for mobile */
    gap: 20px; /* Space between cards */
    padding: 20px;
}

.card {
    background-color: #00FFFF;
    box-shadow: 0 2px 4px rgba(0,0,0,.5);
    border-radius: 20px;
    justify-content: center;
    align-items: center ;
    overflow: hidden;
    position: relative;
    padding-top: 120.25%; /* Aspect ratio */
    
}
.card:hover{
    transform: scale(1.03);
    transition: .25s;
    box-shadow: 0 4px 8px rgba(8, 5, 5, 0.5);
}

.card h2 {
    position: absolute;
    top: 0;
    left: 10px;
    width: 90%;
    border-radius: 20px;
    padding: 5px;
    display: flex;
    background-color: azure;
    flex-direction: row;
}
.card  p {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0px;
    padding: 20px;
    display: flex;
    /* background-color: rgb(199, 66, 84); */
    flex-direction: row;
}

/* Responsive adjustments */
@media (min-width: 576px) { /* Tablets */
    .container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 768px) { /* Notebooks */
    .container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 992px) { /* Desktops and larger devices */
    .container {
        grid-template-columns: repeat(4, 1fr);
    }
}
